.app {
  text-align: center;
  display: flex;
  flex-direction: column;

  &Content {
    padding: 16px 0;
  }

  &Header {
    font-size: 24px;
    line-height: 28px;
    font-weight: 600;
    padding: 16px 0;
    background-color: #dff;

    display: flex;

    .title {
      margin-right: -1.75em;
      flex-basis: 100%;
      height: 28px;

      span {
        max-width: 10em;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
      }
    }

    .actions {
      align-self: flex-end;
      margin-right: 1em;

      svg {
        font-size: 0.75em;
        color: #777;
      }
    }
  }

  &Footer {
    font-size: 14px;
    font-weight: 400;
  }
}

